<template>



	<scroll-view scroll-y class="scrollview" :style="'height:'+height+'px'">
		<view class="white_bac">

			<view v-for="(item1,index1) in luyouData_ziduan" :key="index1">

				<view class="linestyle" v-if="item1.card_id ==card_id">


					<view class="text_title ">字段{{item1.ziduan_id}}: </view>
					<view class="text_view_con"> {{item1.ziduan_zhongwen}} </view>
					<view v-if="(!item1.shanchuflag)" class="icontext text_view_icon green">&#xe625;</view>
					<view v-if="(!item1.shanchuflag)" class="icontext text_view_icon " @click="shanchu(index1)">&#xe66f;</view>
					<view v-if="(item1.shanchuflag)" class="icontext text_view_icon red">&#xe66f;</view>
					<view v-if="(item1.shanchuflag)" class="icontext text_view_icon " @click="bushanchu(index1)">&#xe625;</view>
				</view>
			</view>
			<view style="height: 20upx;"></view>

			<view v-for="(item1,index1) in new_ziduan_list" :key="index1">

				<view class="linestyle">
					<view class="text_title ">字段{{item1.ziduan_id}}: </view>
					<view class="text_view_input ">
						<input :id="index1" :name="item1.ziduan_id" placeholder="字段名" @input="input_change" />
					</view>
				</view>
			</view>
		</view>
		<view class="" style="flex-flow: column;justify-content: center;align-content: center;align-items: center;padding-top: 10upx;padding-bottom: 60upx;">


			<view class="jiahao icontext" @click="add_ziduan">
				&#xe629;
			</view>
			<button @click="formSubmit1" class="tijiao_button_style">
				提交并保存
			</button>



		</view>






	</scroll-view>
</template>
<script>
	import {
		mapState,
		mapMutations
	} from 'vuex'
	export default {
		data() {
			return {
				height: 0,
				card_id: null,
				luyouData_ziduan: null,
				new_ziduan_list: [],
				ziduan_num: 1,
				new_ziduan_num: 1,
			}
		},
		computed: {
			...mapState(['hasLogin', 'userInfo', 'phonenum', 'openid'])
		},
		onLoad: async function(option1) {
         
			this.card_id = option1.card_id;
			this.height = uni.getSystemInfoSync().windowHeight;
			console.log("item_detail：card_id:" + option1.card_id)
			await this.get_hot_card_list_ziduan()
            this.write_ziduan_index();
		},


		methods: {

			
			write_ziduan_index() {
				// var num = 1;
				for (let i = 0; i < this.luyouData_ziduan.length; i++) {

					if (this.luyouData_ziduan[i].card_id == this.card_id) {
						this.luyouData_ziduan[i]['ziduan_id'] = this.ziduan_num;
						this.ziduan_num++;
					}
				}
			},
			input_change(evt) {
				this.new_ziduan_list[evt.currentTarget.id]['ziduan_zhongwen'] = evt.detail.value;
				// console.log(this.new_ziduan_list)
			},
			shanchu(index1) {
				this.luyouData_ziduan[index1]['shanchuflag'] = 'shanchu';
				this.$forceUpdate();
				// console.log(this.luyouData_ziduan);
			},
			bushanchu(index1) {
				this.luyouData_ziduan[index1]['shanchuflag'] = null;
				this.$forceUpdate();
			},

			add_ziduan() {
				if (this.new_ziduan_num < 4) {
					this.new_ziduan_list.push({
						'ziduan_id': this.ziduan_num,
						'ziduan_zhongwen': "",
					})
					// console.log(this.new_ziduan_list)
					this.ziduan_num++;
					this.new_ziduan_num++;
				}

			},
			formSubmit1: function(e) {
				var that = this;

				uni.showLoading({
					title: '上传中'
				})
				setTimeout(function() {
					uni.hideLoading();
				}, 1200);
				for (let i = 0; i < that.new_ziduan_list.length; i++) {
					that.upload_modify(that.new_ziduan_list[i], 'new_ziduan')
				}
				for (let i = 0; i < that.luyouData_ziduan.length; i++) {
					if (that.luyouData_ziduan[i].shanchuflag) {
						that.upload_modify(that.luyouData_ziduan[i], 'shanchu_ziduan')
					}

				}
			},

			upload_modify(ziduan_list, modify_or_new_flag) {
				var that = this;
				uni.request({
					url: 'https://kaikaiomg.applinzi.com/uni_app_upload.php',
					method: "POST",
					data: {
						'form': 'modify_or_build_item',
						'nickName': that.userInfo.nickName,
						'phonenum': that.phonenum,
						'avatarUrl': that.userInfo.avatarUrl,
						'openid': that.openid,
						'card_id': that.card_id,
						'ziduan_id': ziduan_list.ziduan_id,

						'ziduan_zhongwen': ziduan_list.ziduan_zhongwen,
						'modify_or_new_flag': modify_or_new_flag,

					},
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					fail: (e) => {
						// res(e);
					},
					success: (ret) => {
						if (ret.statusCode !== 200) {
							console.log("请求失败:", ret)
						} else {
							console.log("success_modify_data:", ziduan_list.ziduan_zhongwen)
						}
						// res(ret);
					},
				})
			},

		}
	}
</script>

<style>
	.text_view_con {
		padding-left: 20upx;
		width: 400upx;
		overflow: hidden;
	}

	.button_style1 {
		font-size: 34upx;
		color: #F7F7F7;
		background-color: #007AFF;
		border-radius: 20upx;
		width: 130upx;
		height: 53upx;
		justify-content: center;
		align-content: center;
		line-height: 53upx;
	}

	.down_title {
		position: relative;
		top: 10upx;
	}



	.text_view_input {
		width: 500upx;
		overflow: hidden;
	}

	.text_title {
		width: 120upx;

	}

	.text_view_icon {
		padding-left: 24upx;
		color: #8F8F94;
	}

	.red {
		color: red;
	}

	.green {
		color: green;
	}

	.scrollview {
		display: flex;
		flex-flow: column nowrap;
		width: 750upx;
	}
</style>



